.flashcard-container {
   display: flex;
   flex-direction: column;
   align-items: center;
    height: 60vh;
    width: 60%;
    margin: 10px auto;
    background-color: #929292;
    font-family: 'Arial', sans-serif;
    color: white;
    padding: 10px;
    border-radius: 20px;
  }
  
  .loading,
  .error,
  .placeholder {
    font-size: 1.2rem;
    color: white;
    text-align: center;
  }
  
  .flashcard {
    width: 60%;
    height: 60%;
    perspective: 10000px;
    cursor: pointer;
    margin: 0;
  }
  
  .flashcard-front,
  .flashcard-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
    font-family: 'Arial', sans-serif;
    font-size: 1rem;
    padding: 20px;
    transition: transform 1s;
  }
  
  .flashcard-front {
    background-color: #ffffff;
    color: #333;
  }
  
  .flashcard-back {
    background-color: #4a90e2;
    color: #ffffff;
    transform: rotateY(180deg);
  }
  
  .flashcard:hover .flashcard-front {
    transform: rotateY(180deg);
  }
  
  .flashcard:hover .flashcard-back {
    transform: rotateY(0);
  }
  
  .flashcard h3 {
    margin: 0 0 10px;
    font-size: 1.5rem;
  }
  
  .flashcard p {
    margin: 0;
    text-align: center;
  }
  
  .newQuestion{
    margin-top: 3px;
    background: rgb(187, 187, 187);
    width: 20%;
    padding: 15px;
    color:rgb(0, 0, 0);
    border-radius: 20px;
    cursor: pointer;
   border: none;
   box-shadow: 1px 1px 1px gray;
   font-size: 17px;
  }
  .newQuestion:hover{
    background: gray;
  }